import { useState } from "react";
import Cookies from "universal-cookie";
import axios from 'axios';
import { useNavigate } from "react-router-dom";
const cookies = new Cookies(); 

const Login = () => {
    const navigate = useNavigate();
    const [credentials, setCredentials] = useState({
        username: "",
        password: ""
    });
    const [loginError, setLoginError] = useState(null);
    const handleChange = (e) => {
        const {name, value} = e.target;
        setCredentials(prev => {
            return {...prev, [name]: value}
        });
    }
    const loginUser = () => {
        const axiosConfig = {
            method: "post",
            url: "https://regan-blog-api.herokuapp.com/api/login",
            data: {
                username: credentials.username,
                password: credentials.password
            }
        }
        axios(axiosConfig)
        .then(response => {
            cookies.set("TOKEN", response.data.token, {path: "/blog"});
            cookies.set("USER", response.data.user, {path: "/blog"});
            navigate("/posts");
        })
        .catch(err => {
            setLoginError(err.response.data.message);
        });
    }
    const onSubmit = (e) => {
        e.preventDefault();
        loginUser();
        setCredentials({
            username: "",
            password: ""
        });
    }
    return (
        <div className="signup mt-4 d-flex justify-content-center align-items-center">
            <form method="get" className="p-4" onSubmit={onSubmit}>
                {loginError ? <div className="login-error text-danger mb-2 pt-1 pb-1">{loginError}</div> : null}
                <h3>Login</h3>
                <div className="form-group">
                    <label htmlFor="username">Username</label>
                    <input value={credentials.username} required type="text" onChange={handleChange} name="username" className="form-control" id="username" aria-describedby="Username" placeholder="John101" />
                </div>
                <div className="form-group mt-2">
                    <label htmlFor="password">Password</label>
                    <input value={credentials.password} onChange={handleChange} required type="password" name="password" className="form-control" id="password" aria-describedby="Password" placeholder="Password" />
                </div>
                <button className="btn btn-warning mt-3" type="submit">Login</button>
            </form>
        </div>
    )
}
export default Login;