import dateFormatter from "../utilities/dateFormatter";
const Comment = (props) => {
    return (
        <div className="comment">
            <h5>
                {props.comment.text}
            </h5>
            <div className="author-date">
                <p>{props.comment.author}</p>
                <p>{dateFormatter(props.comment.date)}</p>
            </div>
        </div>
    )
}
export default Comment;