import axios from "axios"
import { useNavigate } from "react-router-dom";
import { useState } from "react";
const Signup = () => {
    const navigate = useNavigate();
    const [credentials, setCredentials] = useState({
        username: "",
        password: ""
    });
    const [signupError, setSignupError] = useState(null);
    const handleChange = (e) => {
        const {name, value} = e.target
        setCredentials(prev => {
            return {...prev, [name]: value}
        });
    }
    const signupUser = (e) => {
        e.preventDefault();
        const axiosConfig = {
            method: "post",
            url: "https://regan-blog-api.herokuapp.com/api/signup",
            data: {
                username: credentials.username,
                password: credentials.password
            }
        }
        axios(axiosConfig)
        .then(response => { navigate("/login");})
        .catch(err => {setSignupError(err.response.data.message)});
        setCredentials({username: "", password: ""});
    }
    
    return (
        <div className="signup mt-4 d-flex justify-content-center align-items-center">
            <form method="get" className="p-4" onSubmit={signupUser}>
                {signupError ? <div className="login-error text-danger mb-2 pt-1 pb-1">{signupError}</div> : null}
                <h3>Signup</h3>
                <div className="form-group">
                    <label htmlFor="username">Username</label>
                    <input required value={credentials.username} onChange={handleChange} type="text" name="username" className="form-control" id="username" aria-describedby="Username" placeholder="John101" />
                </div>
                <div className="form-group mt-2">
                    <label htmlFor="password">Password</label>
                    <input required value={credentials.password} onChange={handleChange} type="password" name="password" className="form-control" id="password" aria-describedby="Password" placeholder="Password" />
                </div>
                <button className="btn btn-warning mt-3" type="submit">Signup</button>
            </form>
        </div>
    )
}
export default Signup;