import { Link } from "react-router-dom";
import dateformat from "../utilities/dateFormatter"
const Card = (props) => {
    return (
        <div className="card"> 
            <h2>{props.post.title}</h2>
            <h5 className="mt-2 mb-2">{props.post.subtitle}</h5>
            <p dangerouslySetInnerHTML={ { __html: `${props.post.body.substring(0, 100)}...`} }></p>
            <div className="mt-4 mb-4">
                <Link className="btn btn-warning" to={`/post/${props.post._id}`} >Read</Link>
            </div>
            <div className="post-info d-flex justify-content-between align-items-center mt-4">
                <p>Posted by <span>{props.post.author.username}</span></p>
                <p>Posted on <span>{dateformat(props.post.date)}</span></p>
            </div>
        </div>
    )
}

export default Card;