import { Editor } from '@tinymce/tinymce-react';
import { useRef } from 'react';
import Cookies from "universal-cookie";
import { useNavigate } from "react-router-dom";
import axios from "axios";
const cookies = new Cookies();

const CreatePost = () => {
    const navigate = useNavigate();
    const editorRef = useRef(null);
    const titleRef = useRef(null);
    const subtitleRef = useRef(null);
    const token = cookies.get("TOKEN");
    const savePost = () => {
        const axiosConfig = {
            method: "post",
            url: "https://regan-blog-api.herokuapp.com/api/post/create",
            data: {
                title: titleRef.current.value,
                subtitle: subtitleRef.current.value,
                body: editorRef.current.getContent(),
            },
            headers: {
                Authorization: `Bearer ${token}`
            } 
        }

        axios(axiosConfig)
        .then(response => {
            if (response.data.post._id) {
                navigate(`/post/${response.data.post._id}`);
            }
        })
        .catch(err => {
            console.log(err)
        });
    }
    const onSubmit = (e) => {
        e.preventDefault();
        savePost();
        titleRef.current.value="";
        subtitleRef.current.value="";
        editorRef.current.value="";
       }
    return (
        <div className="create-post mt-4 d-flex justify-content-center align-items-center">
            <form method="get" className="p-4" onSubmit={onSubmit}>
                <h3>Create Post</h3>
                <div className="form-group">
                    <label htmlFor="title">Title</label>
                    <input required type="text" ref={titleRef} name="title" className="form-control" id="title"  />
                </div>
                <div className="form-group">
                    <label htmlFor="subtitle">Subtitle</label>
                    <input required type="text" ref={subtitleRef} name="subtitle" className="form-control" id="subtitle"  />
                </div>
                <div className="form-group">
                    <label htmlFor="body">Body</label>
                    <Editor
                        onInit={(evt, editor) => editorRef.current = editor}
                        apiKey="v3uhpb3llhdi9yj0e1wsas4yg9zsxx1l57uvjlk8xma37tx0"
                        init={{
                        height: 200,
                        menubar: false,
                        plugins: [
                            'advlist', 'autolink', 'lists', 'link', 'image', 'charmap',
                            'preview', 'anchor', 'searchreplace', 'visualblocks', 'code', 'fullscreen',
                            'insertdatetime', 'media', 'table', 'code', 'help', 'wordcount'
                        ],
                        toolbar: 'undo redo | formatselect | ' +
                        'bold italic backcolor | alignleft aligncenter ' +
                        'alignright alignjustify | bullist numlist outdent indent | ' +
                        'removeformat | help',
                        content_style: 'body { font-family:Helvetica,Arial,sans-serif; font-size:14px }'
                        }}
                    />
                </div>
                <button className="btn btn-warning mt-3" type="submit">Create</button>
            </form>
        </div>
    )
}

export default CreatePost;