import { Link } from "react-router-dom";

const Home = () => {
    return (
        <div className="home mt-4">
            <h3>Welcome to Tech Reactor</h3>
            <p>
                You'll find articles about all tech topics you want. 
                You can post your own tech content here.
            </p>
            <Link className="btn btn-warning" to={`/signup`}>Get started</Link>
        </div>
    )
}

export default Home;