import { useParams } from "react-router-dom";
import axios from 'axios';
import uniqid from "uniqid";
import dateformat from "../utilities/dateFormatter"
import { useState, useEffect } from 'react';
import Comment from "../components/Comment";
import Cookies from "universal-cookie"
const cookies = new Cookies()

const SinglePost = () => {
    const {id} = useParams();
    const [post, setPost] = useState(null);
    const [comments, setComments] = useState(null);
    const [commentResponse, setResponse] = useState(null);
    const [username, setUsername] = useState("");
    const [commentText, setCommentText] = useState("");
    const user = cookies.get("USER");
    
    const baseUrl = `https://regan-blog-api.herokuapp.com/api/post/${id}`
    // Fetch Post
    const fetchPost = () => {
        axios.get(baseUrl)
            .then(response => {
                setPost(response.data.post)
                setComments(response.data.comments);
            }
            );
    }
    useEffect(() => {
        fetchPost()
    }, []);

    // Add comment
    const onSubmitForm = (e) => {
        e.preventDefault()
        const axiosConfig = {
            method: "post",
            url: `https://regan-blog-api.herokuapp.com/api/post/${id}/comments`,
            data: {
                author: username,
                text: commentText
            }
        };
        axios(axiosConfig)
        .then(response => {
            fetchPost();
            setUsername("");
            setCommentText("");
            setResponse(response.data.message);
        }).catch(err => {console.log(err)});
    }
    //Unpublish post
    const unpublish = () => {
        axios.post(`${baseUrl}/unpublish`)
            .then(response => {
                if (!response.data.results.published) {
                    fetchPost();
                };
            }
        );
    }
    // Publish Post
    const publishPost = () => {
        axios.post(`${baseUrl}/publish`)
        .then(response => {
            if (response.data.results.published) {
                fetchPost();
            };
        });
    }
    // Delete Post
    const deletePost = () => {
        axios.delete(`${baseUrl}/delete`)
        .then(response => {
            if(response.data.removed) {
                window.location.href = "/blog/posts";
            }
        })
        .catch(err => {console.log(err)});
    }
    return (
        <div className="single-post mt-4 ">
            {post ? 
                <div>
                    <div className="post-header mb-4">
                        <h2>{post.title}</h2> 
                        <h4>{post.subtitle}</h4>
                        <p>Posted by: {post.author.username}</p>
                        <p>Posted on: {dateformat(post.date)}</p>
                    </div>
                    <div className="post-body mb-3">
                        <div className="mb-3">
                            {user && user._id === post.author._id ? 
                                <>
                                    <button onClick={deletePost} className="btn btn-danger mt-3 me-4">Delete Post</button> 
                                    {!post.published ? <button onClick={publishPost} className="btn btn-primary mt-3">Publish</button> :
                                    <button onClick={unpublish} className="btn btn-outline-secondary mt-3">Unpublish</button> }
                                </>
                                : null
                            }
                        </div>
                        <p className="mb-3" dangerouslySetInnerHTML={ { __html: post.body} }></p>
                        <div className="comments-section">
                            <div className="add-comment mt-4 mb-4">
                                <form method="GET" onSubmit={onSubmitForm}>
                                    <h6>Add Comment</h6>
                                    {commentResponse ? <p className="response">{commentResponse}</p>: null}
                                    <div className="form-group">
                                        <label htmlFor="username">Username</label>
                                        <input required value={username} type="text" onChange={e => setUsername(e.target.value)} name="username" className="form-control" id="username" aria-describedby="Username" placeholder="john101" />
                                    </div>
                                    <div className="form-group mt-2">
                                        <label htmlFor="comment">Comment</label>
                                        <textarea required value={commentText} type="text" onChange={e => setCommentText(e.target.value)} name="comment" className="form-control" id="comment" aria-describedby="Comment" placeholder="Comment Here" />
                                    </div>
                                    <button className="btn btn-secondary mt-3" type="submit">Add Comment</button>
                                </form>
                            </div>
                            <h3 className="mb-4">Comments</h3>
                            <div className="comments">
                                {comments ? comments.map(comm => <Comment key={uniqid()} comment={comm}/>): null}
                            </div>
                        </div>
                    </div>
                </div>
            : null}
        </div>
    )
}

export default SinglePost
;